/* Обёртка навигации */

.menu-nav-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  width: 100%;
  flex-basis: auto;
  height: 20vh;
  background-color: black;
}

@media (--mobile) {
  .menu-nav-wrapper {
    display: none;
  }
}

.menu-nav-mobile-wrapper {
  display: none;
}

@media (--mobile) {
  .menu-nav-mobile-wrapper {
    position: fixed;
    top: 35vh;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    align-content: center;
    width: 100%;
    flex-basis: auto;
    height: 13%;
    background-color: black;
    overflow: auto;
    transition-property: top, position;
    transition-duration: 0.25s;
    transition-timing-function: ease-out;
    z-index: 5;
  }
  .menu-nav-mobile-wrapper-no-hamburger {
    top: 17%;
  }
  .menu-nav-mobile {
    position: relative;
    left: 170px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    align-content: center;
    min-width: 700px;
    flex-grow: 1;
  }
  .menu-nav-mobile a {
    font-size: 1rem;
    z-index: 2;
  }
  .menu-nav-mobile a:last-child {
    margin-right: 3rem;
  }
  .menu-nav-mobile a:first-child {
    margin-left: 3rem;
  }
  .menu-nav-mobile__separator {
    background-color: #898989;
    width: 1px;
    height: 1.2rem;
  }
  .menu-nav-mobile__link {
    color: white !important;
  }
  .menu-nav-mobile__link_actual {
    color: var(--ginzaRed) !important;
  }
}

/* Блок с навигацией по видам блюд */

.menu-nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  max-width: 80%;
  flex-grow: 1;
}

/* Ссылки в блоке навигации по видам блюд */

.menu-nav a {
  font-size: 1rem;
  color: white;
  z-index: 2;
}

.menu-nav a:visited {
  color: white;
}

.menu-nav a:hover {
  color: var(--ginzaRed);
}

/* Разделитель цветов */

.menu-nav__separator {
  background-color: #898989;
  width: 1px;
  height: 1.2rem;
}

/* Обёртка информации о блюде дня */

.dish-of-the-day-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  width: 100%;
  height: 80vh;
}

/* Изображение слева */

.left-image {
  order: 1;
  width: 50%;
  height: 100%;
}

.left-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin-bottom: -5px;
}

/* Информация о блюде дня */

.dish-of-the-day {
  order: 2;
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  text-align: center;
  z-index: 1;
}
/* Фоновый паттерн */
.dish-of-the-day::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  opacity: .03;
  z-index: -1;
  background-image: url('../img/pattern.svg');
  background-repeat: repeat;
  background-size: 1100px;
}

@media (--mobile) {
  .left-image {
    width: 100%;
    height: 70vh;
    order: 2;
  }
  .left-image img {
    height: 100%;
  }
  .dish-of-the-day {
    height: auto;
    width: 100%;
    order: 1;
  }
  .dish-of-the-day-wrapper {
    height: auto;
  }
  .dish-of-the-day::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: .03;
    z-index: -1;
    background-image: url('../img/pattern.svg');
    background-repeat: repeat;
    background-size: 1100px;
  }
}

/* Название блока БЛЮДО ДНЯ */

.dish-of-the-day__block-title {
  font-family: 'Futura Medium';
  font-size: 0.875rem;
  letter-spacing: 4.6px;
  width: 122px;
  max-width: 90%;
  padding-left: 3.8px; /* Компенсирует линий отступ справа из-за леттерпспейса */
  margin-left: auto;
  margin-right: auto;
  margin-top: 15vh;
  margin-bottom: 0.5rem;
}

/* Подчёркивание */

.dish-of-the-day .underline {
  height: 1.75px;
  width: 120px;
  max-width: 90%;
  margin-left: auto;
  margin-right: auto;
  background-color: var(--ginzaRed);
  margin-bottom: 10vh;
}

/* Размер блока article */
.dish-of-the-day article {
  width: 100%;
  height: 100%;
}

/* Название блюда дня */

.dish-of-the-day__dish-title {
  max-width: 90%;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.75rem;
  color: black;
}

/* Описание блюда дня */

.dish-of-the-day__dish-description {
  width: 75%;
  margin-left: auto;
  margin-right: auto;
  font-size: 1rem;
}

@media (--mobile) {
  .dish-of-the-day__block-title {
    margin-top: 3rem;
  }
  .dish-of-the-day .underline {
    margin-bottom: 3rem;
  }
  .dish-of-the-day__dish-description {
    font-size: 0.875rem;
    line-height: 1.4;
    margin-bottom: 3rem;
  }
}

/* Стили меню для видов блюд */

/* Обёртка для пречня блюд */
.dishes-wrapper {
  height: 80vh;
  width: 100%;
  overflow: scroll;
}

@media (--mobile) {
  .dishes-wrapper {
    height: 70vh;
    overflow: visible;
  }
}

.about-dish {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 88%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5vh;
  margin-bottom: 5vh;
}

@media (--mobile) {
  .about-dish {
    width: 90%;
  }
}

.about-dish__img {
  width: 142px;
  height: 142px;
  margin-right: 6%;
}

.about-dish__img img {
  width: 142px;
  height: 142px;
}

@media (--mobile) {
  .about-dish {
    width: 90%;
  }
  .about-dish__img {
    width: 80px;
    height: 80px;
    margin-right: 6%;
  }
  .about-dish__img img {
    width: 80px;
    height: 80px;
  }
  .about-dish:first-child {
    margin-top: 10vh;
  }
  .about-dish:last-child {
    margin-bottom: 8vh;
  }
}

.about-dish-information {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-grow: 1;
  justify-content: flex-start;
  align-items: stretch;
  align-content: space-between;
}
.about-dish__title {
  width: 100%;
  margin-top: 0;
  margin-bottom: 0rem;
}
@media (max-width: 1000px) {
  .about-dish__title {
    margin-bottom: 0.5rem;
  }
}
.about-dish__description {
  flex-basis: 60%;
  max-width: 700px;
  flex-grow: 4;
  margin-top: 0;
  margin-bottom: 1.25rem;
}
.about-dish__price {
  font-family: 'EB Garamond' !important;
  font-size: 1.25rem;
  text-align: center;
  flex-basis: 15%;
  flex-grow: 1;
  margin-top: -3px;
  margin-bottom: 0;
}
.about-dish__price span {
  font-family: 'Futura Book';
  color: #a7a7a7;
  font-size: 0.875rem;
}
.about-dish__characteristics {
  font-size: 0.875rem;
  color: #a7a7a7;
  flex-basis: 60%;
  max-width: 700px;
  flex-grow: 4;
  margin-top: 0;
  margin-bottom: 0;
}
@media (--mobile) {
  .about-dish__title {
    font-size: 18px;
    color: black;
  }
  .about-dish__description  {
    font-size: 12px;
    line-height: 13px;
    margin-bottom: 18px;
  }
  .about-dish__price {
    margin-top: -38px;
  }
  .about-dish__characteristics {
    font-size: 12px;
  }
}
.about-dish-styling-line-wrapper {
  flex-basis: 15%;
  flex-grow: 1;
  height: 1px;
  align-self: flex-end;
}
.about-dish-styling-line-wrapper__line {
  height: 1px;
  width: 35px;
  margin-left: auto;
  margin-right: auto;
  background-color: var(--ginzaRed);
}
@media (max-width: 1200px) {
  .about-dish-styling-line-wrapper {
    display: none;
  }
}
@media (--mobile) {
  .about-dish-styling-line-wrapper {
    display: block;
    position: absolute;
    top: 35px;
    right: 2.5%;
    flex-basis: 15%;
    flex-grow: 1;
    height: 1px;
    align-self: flex-end;
  }
  .about-dish-styling-line-wrapper__line {
    height: 1px;
    width: 35px;
    margin-left: auto;
    margin-right: auto;
    background-color: var(--ginzaRed);
  }
}
@media (--mobile) {
  .dish-divider {
    width: 100%;
    height: 1px;
    background-color: #ebebeb;
    opacity: 0.4;
  }
}
