.owl-dot {
  width: 8px;
  height: 8px;
  border-radius: 4px;
  background-color: color(white alpha(50%));
  margin-left: 12px;
  margin-right: 12px;
  transition: all, var(--time), ease;
}

.owl-dot:hover {
  background-color: color(white alpha(75%));
}

.active {
  background-color: color(white alpha(100%));
}

.owl-dots {
  position: absolute;
  bottom: 8%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  width: 100%;
}
