@import "variables.css";
@import "fontfaces.css";
@import "index.css";
@import "news.css";
@import "menu.css";
@import "club.css";
@import "contacts.css";

@import "owl.carousel.css";
@import "owl.theme.default.css";

html,
body {
height: 100%;
min-height: 100%;
}

body {
  margin: 0 0 0 0;
  padding: 0 0 0 0;
}

/* Определяем шрифты по умолчанию */

p, a {
  font-family: sans-serif;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h5 {
  font-family: serif;
  font-weight: 400;
}


/* Определяем шрифты, которые устанавливаются
после загрузки шрифтовых файлов */

.fonts-loaded p, a {
  font-family: 'Futura Book', sans-serif;
  font-weight: 400;
}

.fonts-loaded h1,h2,h3,h4,h5,h6,button {
  font-family: 'EB Garamond', serif;
  font-weight: 400;
}

/* Базовое поведение ссылок */

a {
  color: black;
  text-decoration: none;
  transition: all, var(--time), ease;
}

a:visited {
  color: black;
}

a:hover, a:active, a:focus  {
  color: var(--ginzaRed);
}

/* Класс для ссылки, на которой сейчай находится посетитель */
.actual {
  color: var(--ginzaRed) !important;
}

/* Базовый блок для контента  */

.content-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
    height: 100%;
    transition-property: height, top;
    transition-duration: 0.25s;
    transition-timing-function: ease-out;
}

/* Обёртка контента страницы на мобильном */
@media (--mobile) {
  .content-wrapper {
    width: 100vw;
  }
  .content-wrapper-fixed-hamburger {
    position: relative;
    height: auto;
    top: 35%;
  }
  .content-wrapper-no-hamburger {
    position: relative;
    height: auto;
    top: 17%;
  }
  .content-wrapper-menu-fixed-hamburger {
    position: relative;
    height: auto;
    top: 48%;
  }
  .content-wrapper-menu-no-hamburger {
    position: relative;
    top: 30%;
  }
}

@media (--small) {
  .content-wrapper {
    width: 65%;
  }
}

@media (--medium) {
  .content-wrapper {
    width: 70%;
  }
}

@media (--large) {
  .content-wrapper {
    width: 75%;
  }
}

@media (--xlarge) {
  .content-wrapper {
    width: 77.5%;
  }
}

/* Базовый шрифт для статьи */

p {
  font-size: 1rem;
  line-height: 1.75;
  color: black;
  width: 100% /* Решает проблему с бесконечным контейнером для текста в IE11 */
}

h3 {
  font-size: 1.375rem;
  line-height: 1.25;
  color: var(--ginzaRed);
}

/* Подчёркивание под ссылками и кнопками */


.underline {
  width: 100%;
  background-color: #727272;
  transition-property: background-color;
  transition-duration: var(--time);
  transition-timing-function: ease;
}

/* Кнопка ЗАБРОНИРОВАТЬ СТОЛ на панели навигации */
#bookTheTableOnNav {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  font-size: 1.1rem;
  letter-spacing: 2px;
  background-color: transparent;
  color: black;
  border: 0px;
  cursor: pointer;
  transition-property: opacity, color, margin-top;
  transition-duration: var(--time);
  transition-timing-function: ease;
}
/* Меняем свет кнопки для мобильных */
@media (--mobile) {
  #bookTheTableOnNav {
    color: #727272;
    margin-bottom: 2rem;
  }
}
/* Классы для анимирования видимости кнопки при скролле */
.bookTheTableOnNav_visible {
  opacity: 1;
}
.bookTheTableOnNav_invisible {
  opacity: 0;
}
.bookTheTableOnNav_alwaysVisible {
  opacity: 1;
}
/* На мобильном кнопка всегда видима */
@media (--mobile) {
  .bookTheTableOnNav_visible {
    opacity: 1;
  }
  .bookTheTableOnNav_invisible {
    opacity: 1;
  }
}
/* Смена цвета кнопки на ХОВЕР и ФОКУС */
#bookTheTableOnNav:hover, #bookTheTableOnNavAlwaysVisible:hover {
  color: var(--ginzaRed);
}
/* Подчёркивание под кнопкой */
#bookTheTableOnNav .underline {
  height: 1px;
}
/* Смена цвета подчёркивания на ХОВЕР кнопки*/
#bookTheTableOnNav:hover>.underline {
  background-color: var(--ginzaRed);
}
/* Подведение кнопки на НАЖАТИЕ */
#bookTheTableOnNav:active {
  margin-top: 2px;
}
/* Кнопка для сокрытия меню на МОБИЛЬНОМ */
#toggleMobileNav, #toggleMobileNav__line {
  display: none;
}
@media (--mobile) {
  #toggleMobileNav {
    display: block;
    width: 100px;
    height: 25px;
    border: 0;
    background-color: transparent;
  }
  /* Полоска внутри кнопки */
  #toggleMobileNav__line {
    display: block;
    width: 44px;
    height: 2px;
    background-color: var(--ginzaRed);
    margin-left: auto;
    margin-right: auto;
  }
}
/* Класс для скрытия элементов */
@media (--mobile) {
  .hidden {
    display: none !important;
  }
}
