/* Тело index.html */

.general-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: flex-start;
  align-items: flex-start;
  height: 100%;
}

/* Мемяем ориентацию флексбокса на мобильном и фиксируем ширину */
@media (--mobile) {
  .general-container {
    width: 100vw;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: flex-start;
  }
}


/* Контейнер для основной боковой навигации */

.main-nav-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-shrink: 0;
  height: 100%;
  z-index: 0;
  background-color: white;
  transition-property: height;
  transition-duration: 0.25s;
  transition-timing-function: ease-out;
}

@media (--mobile) {
  .main-nav-container {
    /* Финсируем ширину блока навигации на мобильном */
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 20;
    transition-property: height;
    transition-duration: 0.25s;
    transition-timing-function: ease-out;
  }
  /* Финсируем высоту блока навигации с гамбургером */
  .main-nav-container_closed-with-hamburger {
    height: 35%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 20;
  }
  /* Финсируем высоту блока навигации без гамбургера */
  .main-nav-container_closed-no-hamburger {
    height: 17%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 20;
  }
}

@media (--small) {
  .main-nav-container, .logo-and-nav-wrapper {
    flex-basis: 35%;
    width: 35%;
  }
}

@media (--medium) {
  .main-nav-container, .logo-and-nav-wrapper {
    flex-basis: 30%;
    width: 30%;
  }
}

@media (--large) {
  .main-nav-container, .logo-and-nav-wrapper {
    flex-basis: 25%;
    width: 25%;
  }
}

@media (--xlarge) {
  .main-nav-container, .logo-and-nav-wrapper {
    flex-basis: 22.5%;
    width: 22.5%;
  }
}

/* Контейнер для лого и навигации */
.logo-and-nav-wrapper {
  position: fixed;
  top: 20%;
  left: 0%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  transition-property: height;
  transition-duration: 0.1s;
  transition-timing-function: ease-in;
}
.logo-and-nav-wrapper_small {
  height: 80px;
  display: block;
}
@media (--mobile) {
  .logo-and-nav-wrapper {
    position: relative;
    top: 0;
    left: 0;
  }
  .main-nav-container-logo {
    display: block;
  }
}

/* Лого в вонтейнере основной навигации */
.main-nav-container-logo {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
}

.main-nav-container__logo {
  width: 168px;
  height: 75px;
  margin-bottom: 1.5rem;
  /* Анимация при изменении размера лого на мобильном */
  transition-property: margin-bottom;
  transition-duration: 0.25s;
  transition-timing-function: ease-out;
}

@media (--mobile) {
  .main-nav-container__logo_small {
    margin-bottom: 0rem;
  }
}

.main-nav-container__logo:hover {
}

/* Основная боковая навигация */
.main-nav-container__nav {
  display: flex;
  flex-direction: column;
  margin-bottom: 3rem;
}

/* Ссылки на боковой панели навигации */
.main-nav-container__nav a {
  display: block;
  text-align: left;
  font-size: 1.2rem;
  margin-top: 0.875rem;
  margin-bottom: 0.875rem;
  transition-property: color;
  transition-duration: var(--time);
  transition-timing-function: ease;
}
/* Центрирует текст на мобильном
и увеличиваем размер шрифта */
@media (--mobile) {
  .main-nav-container__nav a {
    text-align: center;
    font-size: 1.5rem;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
  }
  .main-nav-container__nav {
    margin-bottom: 2rem;
    margin-top: 1rem;
  }
}

/* Обёртка контента главной страницы */

.main-content-wrapper {
    position: relative;
    /* Анимация при изменении размера блока на мобильном */
    transition-property: height, top;
    transition-duration: 0.1s;
    transition-timing-function: ease-in;
}
/* Обёртка контента главной страницы на мобильном */
@media (--mobile) {
  .main-content-wrapper {
    width: 100vw;
  }
  .main-content-wrapper-fixed-hamburger {
    position: relative;
    height: auto;
    top: 35%;
  }
  .main-content-wrapper-no-hamburger {
    position: relative;
    height: auto;
    top: 17%;
  }
}

@media (--small) {
  .main-content-wrapper {
    width: 65%;
  }
}

@media (--medium) {
  .main-content-wrapper {
    width: 70%;
  }
}

@media (--large) {
  .main-content-wrapper {
    width: 75%;
  }
}

@media (--xlarge) {
  .main-content-wrapper {
    width: 77.5%;
  }
}

/* Полоски для стилизации внутри обёртки контента главной страницы*/

.main-content-wrapper__style-line {
  position: absolute;
  background-color: color(var(--lineGrey) alpha(25%));
  z-index: 20;
}

@media (--mobile) {
  .main-content-wrapper__style-line {
    display: none;
  }
}

.main-content-wrapper__style-line_left {
  left: 20px;
  width: 1px;
  height: 100%;
}

.main-content-wrapper__style-line_right {
  right: 20px;
  width: 1px;
  height: 100%;
}

.main-content-wrapper__style-line_top {
  position: fixed;
  top: 20px;
  width: 100%;
  height: 1px;
}

.main-content-wrapper__style-line_bottom {
  bottom: 20px;
  width: 100%;
  height: 1px;
}

/* Слайдер */

.slider-wrapper {
  position: relative;
}

@media (--mobile) {
}

/* Кнопка "Забронировать стол" над слайдером */

#bookTheTable {
  position: absolute;
  z-index: 10;
  font-size: 1rem;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  border: 0px;
  cursor: pointer;
  transition: all, 0.2s, ease;
}

.bookTheTableHover {
  animation-duration: 0.2s;
  animation-name: bookTheTableHover;
  animation-fill-mode: forwards;
} /* Анимация кнопки при ховере */

@keyframes bookTheTableHover {
  from {
    background-color: rgba(0, 0, 0, 0.6);
    font-size: 1rem;
  }
  to {
    background-color: color(var(--ginzaRed) alpha(80%));
    font-size: 1.1rem;
  }
}

.bookTheTableHoverOut {
  animation-duration: 0.2s;
  animation-name: bookTheTableHoverOut;
  animation-fill-mode: forwards;
} /* Анимация кнопки при снятии ховера */

@keyframes bookTheTableHoverOut {
  from {
    background-color: color(var(--ginzaRed) alpha(80%));
    font-size: 1.125rem;
  }
  to {
    background-color: rgba(0, 0, 0, 0.6);
    font-size: 1rem;
  }
}

.slider-wrapper button:active { /* Анимация кнопки при нажатии */
  margin-top: 0.15rem;
}

/* width < 600 */
@media (--mobile) {
  .slider-wrapper button {
    width: 70%;
    height: 10%;
    top: 45%;
    left: 15%;
    letter-spacing: 1px;
  }
}
/* 600 < width < 900 */
@media (--small) {
  .slider-wrapper button {
    width: calc(70% + ( -30 ) * ((100vw - 37.5rem)/( 56.25 )));
    height: 8%;
    top: 46.5%;
    left: calc(22.5% + (32.5 - 22.5) * ((100vw - 37.5rem)/( 56.25 )));
    letter-spacing: 1px;
  }
}
/* 900 < width < 1200 */
@media (--medium) {
  .slider-wrapper button {
    width: calc(40% + ( -10 ) * ((100vw - 56.25rem)/( 75 )));
    height: 8%;
    top: 46.5%;
    left: calc(32.5% + ( 2.5 ) * ((100vw - 56.25rem)/( 75 )));
    letter-spacing: 1.5px;
  }
}
/* 1200 < width < 1600 */
@media (--large) {
  .slider-wrapper button {
    width: calc(30% + ( -5 ) * ((100vw - 75rem)/( 100 )));
    height: 8%;
    top: 46.5%;
    left: calc(35% + ( -2.5 ) * ((100vw - 75rem)/( 100 )));
    letter-spacing: 2px;
  }
}
/* width > 1600 */
@media (--xlarge) {
  .slider-wrapper button {
    width: 25%;
    height: 8%;
    top: 46.5%;
    left: 37.5%;
    letter-spacing: 2.5px;
  }
}



/* Текст о истории компании */

/* Контейнер */
.article-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  width: 750px;
  max-width: 85%;
  margin-top: 4rem;
  margin-bottom: 4rem;
}

@media (--mobile) {
  .article-wrapper {
    margin-top: 2rem;
    margin-bottom: 2rem;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (--small) {
  .article-wrapper {
    margin-left: auto;
    margin-right: auto;
  }
}

@media (--medium) {
  .article-wrapper {
    margin-left: auto;
    margin-right: auto;
  }
}

@media (--large) {
  .article-wrapper {
    margin-left: calc(5% + ( 7.5 ) * ((100vw - 75rem)/( 100 )));
  }
}

@media (--xlarge) {
  .article-wrapper {
    margin-left: 12.5%;
  }
}

/* Обёртка заголовка */

.h-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
}

/* Полоска для стилизации заголовка */
.h-wrapper__style-rectangle {
  width: 50px;
  height: 1px;
  background-color: var(--styleRectangle);
  margin-right: 1rem;
}



/* Футер для стилизации */

/* Футер с красным фоном */
.main-content-wrapper__footer {
  width: 100%;
  height: 10vw;
  background-color: var(--ginzaRed);
}
/* Паттерн поверх футера */
.main-content-wrapper__pattern {
  width: 100%;
  height: 100%;
  background-image: url(../img/pattern.svg);
  background-repeat: repeat;
  opacity: 0.03;
}

.main-content-wrapper__pattern-small {
  display: flex;
  width: 100%;
  height: 100%;
  background-image: url(../img/pattern.svg);
  opacity: 0.03;
  background-size: 1100px;
}
@media (--mobile) {
  .main-content-wrapper__pattern-small {
    display: none;
  }
}
