.contacts-wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap:wrap;
  align-items: flex-start;
  align-content: flex-start;
}

.contacts {
  order: 1;
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
}

@media (--mobile) {
  .contacts-wrapper {
    height: 60vh;
  }
  .contacts {
    order: 2;
    width: 100%;
    height: 40%;
    justify-content: space-around;
    margin-top: 1rem;
  }
}

.map {
  order: 2;
  width: 50%;
  height: 100%;
  display: block;
}

@media (--mobile) {
  .map {
    order: 1;
    width: 100%;
    height: 50%;
  }
}

.contacts__adress {
  margin-top: 33vh;
  font-size: 1.25rem;
  font-family: 'Futura Book';
  font-style: normal;
  text-align: center;
}
.contacts__tel {
  font-size: 1.25rem;
  font-family: 'Futura Book';
  color: var(--ginzaRed);
  margin-top: 1vh;
}
.contacts__workhours {
  font-size: 0.875rem;
  color: #afafaf;
  margin-top: 3vh;
  margin-bottom: 0rem;
  text-align: center;
}
.contacts__workhours2 {
  font-size: 1rem;
  color: #afafaf;
  text-align: center;
  margin-top: 0;
  margin-bottom: 24vh;
}
.contacts-social svg {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}
.contacts-social svg #tripa,
.contacts-social svg #insta,
.contacts-social svg #vk,
.contacts-social svg #fb
 {
  transition-property: fill;
  transition-duration: var(--time);
  transition-timing-function: ease;
}
.contacts-social svg:hover #tripa,
.contacts-social svg:hover #insta,
.contacts-social svg:hover #vk,
.contacts-social svg:hover #fb
 {
  fill: #afafaf;
}

@media (--mobile) {
  .contacts__adress {
    margin-top: 1rem;
    font-size: 1rem;
  }
  .contacts__workhours {
    margin-top: 0.75rem;
    line-height: 1.2;
  }
  .contacts__workhours2 {
    margin-bottom: 0.75rem;
    line-height: 1.2;
  }
  .contacts__tel {
    margin-top: 0rem;
    font-size: 1rem;
  }
}
