:root {
  /* Colors */
  /* Фирменный цвет */
  --ginzaRed: #ff4040;
  /* Серые линии по краям слайдера */
  --lineGrey: #ebebeb;
  /* Стилизация заголовка на главной */
  --styleRectangle: #d4d4d4;
  /* Базовая скорость транзишена */
  --time: 0.2s;
  --grotesque: 'Fira Sans';
}


/* Custom media queries */

/* width < 600 */
@custom-media --mobile (width <= 37.5rem);
/* 600 < width < 900 */
@custom-media --small (width > 37.5rem) and (width <= 56.25rem);
/* 900 < width < 1200 */
@custom-media --medium (width > 56.25rem) and (width <= 75rem);
/* 1200 < width < 1600 */
@custom-media --large (width > 75rem) and (width <= 100rem);
/* width > 1600 */
@custom-media --xlarge (width > 100rem);
