/* Стилизованный хэдер */

.news-header {
    height: 14.5vw;
    width: 100%;
    position: fixed;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
    background-color: white;
    z-index: 10;
}

/* Обёртка для паттерка с красным бэкграундом */

.news-header-pattern-wrapper {
  height: 5vw;
  width: 100%;
  background-color: var(--ginzaRed);
}



@media (--mobile) {
  /* На мобильнов панель навигации меньше и по центру */
  .news-header {
    height: 11vw;
    justify-content: center;
    align-items: center;
    align-content: center;
  }
  /* Полоса навигации не отображается */
  .news-header-pattern-wrapper {
    display: none;
  }
}

/* Слой с паттерном */
.news-header__pattern {
  width: 100%;
  height: 100%;
  background-image: url(../img/pattern.svg);
  opacity: 0.06;
}

/* Контейнер для навигации новости/акции */
.news-header-nav, .news-header-nav-mobile {
  width: 750px;
  max-width: 85%;
  height: 9.5vw;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-end;
  align-content: flex-end;
  margin-bottom: 4vw;
}

.news-header-nav-mobile {
  display: none;
}

@media (--mobile) {
  .news-header {
    display: none;
  }
}

@media (--mobile) {
  .news-header-nav-mobile {
    display: flex;
    height: 5vw;
    width: 90%;
    justify-content: center;
    margin-bottom: 2vw;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1.5rem;
  }
  .news-header-nav-mobile_hide {
    display: none;
  }
}

/* Ссылки в контейнере навигации */
.news-header-nav a, .news-header-nav-mobile a {
  font-family: 'EB Garamond';
  font-size: 1.375rem;
}

/* Правый отступ для первой ссылки */
.news-header-nav a:first-child, .news-header-nav-mobile a:first-child {
  margin-right: 2rem;
}

.news-header-nav__go-back-button {
  width: 10px;
  position: relative;
  top: 0px;
  left: -4vw;
  margin: 0 0 0 0 !important;
}

.news-header-nav__news-title {
  position: relative;
  left: -10px; /*  */
  top: 0px;
  margin-top: 0px;
  margin-bottom: 0px;
}

/* Подчёркивание */

a .underline {
  height: 0.5px;
  background-color: var(--styleRectangle);
}

/* Ховер на ссылке меняет цвет подчёркивания */
.news-header-nav a:hover>.underline {
  background-color: var(--ginzaRed);
}



/* Контейнер для превью новостей */

.article-previews-wrapper, .news-article-wrapper {
  margin-top: 14.5vw; /* Отступ от фиксированного хедера */
  width: 750px;
  max-width: 85%;
}

@media (--mobile) {
  .article-previews-wrapper {
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    max-width: 90%;
  }
  .news-article-wrapper {
    width: 100%;
    max-width: 100%;
    margin-top: 0;
    margin-bottom: 5vh;
    margin-left: auto;
    margin-right: auto;
  }
}

/* Адаптивный отступы контейнера с навигацией и превью новостей */
@media (--small) {
  .news-header-nav, .article-previews-wrapper, .news-article-wrapper {
    margin-left: 5vw;
  }
}

@media (--medium) {
  .news-header-nav, .article-previews-wrapper, .news-article-wrapper {
    margin-left: 5vw;
  }
}

@media (--large) {
  .news-header-nav, .article-previews-wrapper, .news-article-wrapper {
    margin-left: calc(5vw + ( 6 ) * ((100vw - 75rem)/( 35 )));
  }
}

@media (--xlarge) {
  .news-header-nav, .article-previews-wrapper, .news-article-wrapper {
    margin-left: 11vw;
  }
}

/* Контейнер для превью ОТДЕЛЬНОЙ новости */

.article-preview {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  margin-bottom: 3rem;
}

@media (--mobile) {
  .article-preview {
    margin-bottom: 2.5rem;
    align-items: flex-start;
    align-content: flex-start;
  }
}

/* Блок для картинки */

.article-preview__img {
  width: 130px;
  height: 130px;
  margin-right: 2.5vw;
  background-color: color(grey alpha(15%));
}

@media (--mobile) {
  .article-preview__img {
    width: 100px;
    height: 100px;
  }
}

/* Обёртка для заголовка и текста превью */

.article-preview-info-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  width: 70%;
}

/* Название статьи */

.article-preview__title {
  margin-top: 0px;
  margin-bottom: 1.5rem;
}

@media (--mobile) {
  .article-preview__title {
    font-size: 1.1rem;
    margin-top: 0px;
    margin-bottom: 0.5rem;
  }
  .article-preview-info-wrapper {
    margin-top: -4px;
  }
}

/* Текст превью */

.article-preview__text {
  margin-top: 0px;
  margin-bottom: 0px;
}

@media (--mobile) {
  .article-preview__text {
    font-size: 0.875rem;
    line-height: 1.2rem;
  }
}


.news-article-wrapper__img {
  width: 100%;
  height: 400px;
}

.news-article-wrapper__img img {
  width: 100%;
  height: 400px;
  object-fit: cover;
  margin-bottom: 1rem;
}

@media (--mobile) {
  .news-article-wrapper__img {
    height: 35vh;
  }

  .news-article-wrapper__img img {
    height: 35vh;
    margin-bottom: 0;
  }
  .news-article-wrapper p {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
  }
}

.news-header-nav__news-title-mobile {
  display: none;
}

@media (--mobile) {
  .news-header-nav__news-title-mobile {
    display: flex;
    position: relative;
    left: 0px; /*  */
    top: 0px;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1rem;
    margin-bottom: 1rem;
    color: black;
  }
}
