.ginzaclub__title, .ginzaclub__call {
  margin-top: 5vh;
}
@media (--mobile) {
  .ginzaclub__title, .ginzaclub__call {
    margin-top: 0vh;
  }
}

.ginzaclib__description {
  width: 90%;
}
@media (--mobile) {
  .ginzaclib__description {
    line-height: 1.5;
  }
}

.ginzaclub-form__submit {
  margin-top: 2vw;
  margin-bottom: 3rem;
}

.news-header picture {
  width: 100%;
  height: 100%;
}

.news-header img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

#ginzaclub-form {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-content: flex-start;
  align-items: flex-start;
  width: 300px;
  max-width: 90%;
}

.ginzaclub-form__input {
  width: 100%;
  height: 2rem;
  margin-bottom: 1rem;
  border: 1px;
  border-bottom-style: solid;
  border-bottom-color: #727272;
  transition-property: border-bottom-color;
  transition-duration: var(--time);
  transition-timing-function: ease;
}


.ginzaclub-form__input:focus {
  border-bottom-color: var(--ginzaRed);
}

.ginzaclub-form__submit {
  font-family: 'Futura Book';
  font-size: 1rem;
  color: white;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  padding-left: 2rem;
  padding-right: 2rem;
  border: 0;
  border-radius: 2px;
  background-color: color(var(--ginzaRed) alpha(100%));
  transition-property: background-color, margin-top;
  transition-duration: var(--time);
  transition-timing-function: ease;
  cursor: pointer;
}

@media (--mobile) {
  .ginzaclub-form__submit {
    margin-bottom: 10vh;
  }
}

.ginzaclub-form__submit:hover {
  background-color: color(var(--ginzaRed) alpha(90%));
}

.ginzaclub-form__submit:active {
  margin-top: 2.1vw;
  background-color: color(var(--ginzaRed) alpha(100%));
}

#cards {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: -1;
}

@media (--mobile) {
  #cards {
    display: none;
  }
}
